.card img {
    width: 80%;
    display: block;
    border-color: white;
    border: 6px solid ;
    border-radius: 6px;
    margin-bottom: 0px;
}
.card{
    position: relative;
}

.card .front {
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}
.flipped .front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}
.card .back {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}
.flipped .back{
    transform: rotateY(90deg);
    transition-delay: 0s;
}